/* Reusable styles */
.button {
  display: inline-block;
  background-color: #0000000d;
  border-radius: 15px;
  padding: 8px 10px;
  margin: 3px 5px;
  user-select: none;
  font-size: calc(8px + 2vmin);
}

.flex {
  display: flex;
}

.vert-center {
  display: flex;
  flex-direction: column;
  align-items: center;
}

/* Game syles */
.App {
  font-size: calc(10px + 2vmin);
  font-family: avenir, sans-serif;
}

.card {
  background-color: #fdfbee;
  min-width: 80vw;
  border-radius: 8px;
  padding: 20px;
}

/* todo: maybe reorganize component styles */

.Jumbotron {
  display: flex;
  justify-content: space-between;
  font-family: avenir heavy,sans-serif;
  margin-bottom: 2vh;
}

/* Beans */
.Beans-container {
  flex-grow: 2;
  margin-left: 1vw;
}

.Beans > .progress-bar {
  background-color: #0000000d;
  border-radius: 15px;
  padding: 8px 10px;
  margin: 3px 5px;
}

/* Upgrade */
.Upgrade-container {
  display: flex;
  flex-wrap: wrap;
}

.Upgrade {
  font-size: calc(2vmin);
}

/* Sundial */
.Sundial {
  background: conic-gradient(orange 25%, #675e97 0 75%);
  width: 20vw;
  height: 20vw;
  border-radius: 50%;
  margin-bottom: 2vh;

  display: flex;
  flex-direction: column;
  justify-content: center;
}

.Hand {
  background: black;
  width: 50%;
  height: 4px;
  border-radius: 98%;
}

@media (prefers-reduced-motion: no-preference) {
  .Hand-container {
    animation: Hand-rotate 30s infinite linear;
  }
}
@keyframes Hand-rotate {
  100% {
    transform: rotateZ(360deg);
  }
}
